#footer {
  height: 40px;
  border-top: 1px solid $black;

  ul {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    max-width: 620px;
    padding: 1px 0 8px 0;
    margin: 0 auto;
  }

  a {
    color: $black;
    font-family: $font-primary;
    font-size: 1rem;
    font-weight: 400;
    line-height: normal;
    text-transform: uppercase;
    text-decoration: none;
    transition: color 0.2s ease-in-out;
  }
}